import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/views/index/Index' //

import CateGory from '@/views/category/CateGory' //

import ShopCart from '@/views/shopCart/ShopCart' //
import ShopCartSettlement from '@/views/shopCart/Sattlement' //

import UserCenter from "@/views/usercenter/UserCenter" //

import AddressList from "@/views/usercenter/address/AddressList" //
import AddressEdit from "@/views/usercenter/address/AddressEdit" // 

import OrderList from "@/views/usercenter/order/OrderList"; //
import OrderDetail from "@/views/usercenter/order/OrderDetail"; // 

import ProductList from "@/views/product/ProductList"; //
import ProductDetail from "@/views/product/ProductDetail"; //
import Settlement from "@/views/product/Settlement";//

import Success from "@/views/pay/Success";//

import SystemMessage from "@/views/usercenter/systemMessage/SystemMessage"; //

import Setting from "@/views/usercenter/setting/Setting";//
import AboutUs from "@/views/usercenter/setting/AboutUs";//
import UserBook from "@/views/usercenter/setting/UserBook";//
import Complaint from "@/views/usercenter/setting/Complaint";//

import Poster from '@/views/usercenter/poster/Poster'//

import Search from '@/views/search/Search'//

import Video from '@/views/video/Video'//
import VideoDetail from '@/views/video/detail/Detail'//

import UserData from '@/views/usercenter/data/UserData'//
import Wallet from '@/views/usercenter/wallet/Wallet'//
import MyTeam from '@/views/usercenter/myTeam/MyTeam'//
import IntegralRecord from '@/views/usercenter/integralRecord/integralRecord'//


import Author from '@/views/public/Author'//
import Recharge from '@/views/usercenter/recharge/Recharge'//
import RechargeLog from '@/views/usercenter/recharge/RechargeLog'//


import NoticeList from '@/views/Notice/List'//
import NoticeDetail from '@/views/Notice/Detail'//

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Index',
            component: Index
        },
        {
            path: '/Category',
            name: 'CateGory',
            component: CateGory
        },
        {
            path: '/ShopCart',
            name: 'ShopCart',
            component: ShopCart
        },
        {
            path: '/ShopCartSettlement',
            name: 'ShopCartSettlement',
            component: ShopCartSettlement
        },
        {
            path: '/UserCenter',
            name: 'UserCenter',
            component: UserCenter
        },
        {
            path: '/AddressList/:type?',
            name: 'AddressList',
            component: AddressList
        },
        {
            path: '/AddressEdit',
            name: 'AddressEdit',
            component: AddressEdit
        },
        {
            path: '/OrderList',
            name: 'OrderList',
            component: OrderList
        },
        {
            path: '/OrderDetail/:loadId',
            name: 'OrderDetail',
            component: OrderDetail
        },
        {
            path: '/ProductList/:loadId/:type',
            name: 'ProductList',
            component: ProductList
        },
        {
            path: '/ProductDetail/:loadId',
            name: 'ProductDetail',
            component: ProductDetail
        },
        {
            path: '/Settlement',
            name: 'Settlement',
            component: Settlement
        },
        {
            path: '/Success',
            name: 'Success',
            component: Success
        },
        {
            path: '/SystemMessage',
            name: 'SystemMessage',
            component: SystemMessage
        },
        {
            path: '/Setting',
            name: 'Setting',
            component: Setting
        },
        {
            path: '/AboutUs',
            name: 'AboutUs',
            component: AboutUs
        },
        {
            path: '/UserBook',
            name: 'UserBook',
            component: UserBook
        },
        {
            path: '/Complaint',
            name: 'Complaint',
            component: Complaint
        },
        {
            path: '/Poster',
            name: 'Poster',
            component: Poster
        }, {
            path: '/Search',
            name: 'Search',
            component: Search
        },
        {
            path: '/Video',
            name: 'Video',
            component: Video
        },
        {
            path: '/VideoDetail/:loadId',
            name: 'VideoDetail',
            component: VideoDetail
        },
        {
            path: '/UserData',
            name: 'UserData',
            component: UserData
        },
        {
            path: '/Wallet',
            name: 'Wallet',
            component: Wallet
        },
        {
            path: '/Author',
            name: 'Author',
            component: Author
        },{
            path: '/MyTeam',
            name: 'MyTeam',
            component: MyTeam
        },{
            path: '/Recharge',
            name: 'Recharge',
            component: Recharge
        },{
            path: '/NoticeList',
            name: 'NoticeList',
            component: NoticeList
        },{
            path: '/NoticeDetail/:loadId',
            name: 'NoticeDetail',
            component: NoticeDetail
        },{
            path: '/IntegralRecord/:type',
            name: 'IntegralRecord',
            component: IntegralRecord
        },{
            path: '/RechargeLog',
            name: 'RechargeLog',
            component: RechargeLog
        },
    ]
})



