<template>
    <div>
        <van-tabs sticky v-model="active">
            <van-tab title="">
                <div class="table default-window">
                    <div class="table-cell">
                        <span class="money"></span>
                    </div>
                    <div class="table-cell text-right" style="vertical-align: bottom"><span style="font-weight: bold"
                                                                                            class="font-red">{{balance}}</span>
                        <span></span></div>
                </div>

                <van-radio-group v-model="payWay">
                    <van-cell-group>
                        <van-cell icon="alipay" :label="ali_pay==''?'':ali_pay" title="" clickable @click="payWay = '1'">
                            <template #right-icon>
                                <van-radio checked-color="#ee0a24" name="1"/>
                            </template>
                        </van-cell>
                        <van-cell icon="wechat" title="" clickable @click="payWay = '2'">
                            <template #right-icon>
                                <van-radio checked-color="#ee0a24" name="2"/>
                            </template>
                        </van-cell>
                        <van-cell :label="bank_account==''?'':bank_name+' '+bank_user+' '+bank_account"
                                  icon="card" title="" clickable @click="payWay = '3'">
                            <template #right-icon>
                                <van-radio checked-color="#ee0a24" name="3"/>
                            </template>
                        </van-cell>
                    </van-cell-group>
                </van-radio-group>
                <div class="blank"></div>
                <div class="default-window">
                    <span class="money"></span>
                </div>
                <div>
                    <van-cell-group>
                        <van-field
                            label=""
                            left-icon="after-sale"
                            placeholder=""
                            size="large"
                            type="number"
                            clearable
                            v-model="money"
                        />
                    </van-cell-group>

                </div>
                <div class="font-gary default-window">
                    <p>1.</p>
                    <p>2.=-</p>
                    <p>=</p>
                    <p></p>
                </div>
                <div class="default-window">
                    <van-button round @click="submitWithdraw" type="danger" block></van-button>
                </div>
            </van-tab>
            <van-tab title="">
                <van-cell-group>
                    <van-cell v-for="(item,index) in logs" :key="index" :title="`  ${item.withdraw_num}`" :label="item.time" >
                        <van-tag :type="logType[item.status]">{{logStatus[item.status]}}</van-tag>
                    </van-cell>
                </van-cell-group>
            </van-tab>
        </van-tabs>
    </div>
</template>

<script>
    export default {
        name: "Wallet",
        data() {
            return {
                active:0,

                balance: 0,
                payWay: 0,
                money: '',
                radio: 1,

                bank_name: '',
                bank_user: '',
                bank_account: '',
                ali_pay:'',

                logStatus: {
                    0: '',
                    1: '',
                    2: ''
                },
                logType: {
                    0: 'primary',
                    1: 'success',
                    2: 'danger'
                },
                logs:[]
            }
        },
        mounted() {
            this.loadWallet();
            this.loadUserCenter();
            this.withdrawLog();
        },
        methods: {
            //
            withdrawLog() {
                this.$api('Withdraw/log').then(data => {
                    this.logs = data.withdraw_list;
                    console.log(data);
                });
            },
            //
            loadWallet() {
                this.$api('Withdraw/index').then(data => {
                    this.balance = data.balance;
                });
            },
            //
            loadUserCenter() {
                this.$api('UserData/index').then(data => {
                    let formData = data.user_data;
                    this.bank_name = formData.bank_name;
                    this.bank_user = formData.bank_user;
                    this.bank_account = formData.bank_account;
                    this.ali_pay = formData.ali_pay;
                });
            },
            //
            submitWithdraw() {
                if (this.payWay == 0) {
                    this.$notify('');
                    return;
                }
                this.$dialog.confirm({
                    message: ''
                }).then(() => {
                    let params = {
                        sum: this.money,
                        pay_type: this.payWay
                    };
                    this.$api('Withdraw/create', params).then(data => {
                        this.$toast.success('');
                    });
                }).catch(() => {
                });
            }
        }
    }
</script>

<style scoped>
    .card-window {
        margin: 10px;
        border-radius: 7px;
        background-color: #333333;
        color: #EEEEEE;
        font-size: 20px;
        padding: 20px;

    }

    .money {
        font-size: 20px;
    }
</style>



