import Vue from 'vue'
import vuex from 'vuex'

Vue.use(vuex);

export default new vuex.Store({
    state: {
        name: '',
        age: '',

        //
        appid: '',

        userLevel: 0,

        //token
        token: '',

        //
        isLoginLing: false,
        celebrity_lv: 0,
        identity_lv: 0,
        recordingAddress: false,
        subscribe: 0,

        //sn
        sn: '',

        //
        chooseAddress: {
            name: '',
            phone: '',
            address: '',
            address_id: 0,
            loadDefault: true,
            pick_type: 0,
            load_tpl: false,
            isChoose: false,
        },
        //
        pickAddress: '',

        //
        cateList: [],
        cateId: 0,

        //
        productSattlement:{},
        numSattlement:0,
        skuInfo:{},

        //
        cartInfo:{},

        //
        pickType:'1'
    }
})



