<template>
    <div>
        <van-notice-bar wrapable :scrollable="false" left-icon="volume-o" mode="closeable">
            
        </van-notice-bar>
        <div class="status-window">
            <div style="width: 100%;height: 100px"></div>
        </div>
        <div style="padding: 10px;margin-top: -110px">
            <div class="info-window white-bg">
                <div class="address-window  table">
                    <div class="table-cell address-left">
                        <van-icon class="block" size="25" name="location-o"/>
                    </div>
                    <div class="table-cell">
                        <div><span>{{order.receiver_name}}</span><span style="padding-left: 10px">{{order.receiver_phone}}</span>
                        </div>
                        <div><span class="font-gary">{{order.receiver_address}}</span></div>
                    </div>
                </div>
                <div class="white-bg pay-window">
                    <div class="table">
                        <div class="table-cell address-left">
                            <van-icon class="block" size="25" name="label-o"/>
                        </div>
                        <div class="table-cell">
                            <div><span>{{order.order_no}}</span></div>
                        </div>
                    </div>
                </div>
                <div class="white-bg pay-window">
                    <div class="table">
                        <div class="table-cell address-left">
                            <van-icon class="block" size="25" name="free-postage"/>
                        </div>
                        <div class="table-cell">
                            <div><span>{{order.express_id}}</span></div>
                            <div><span>{{order.express_no||''}}</span></div>
                        </div>
                        <div class="table-cell">
                            <van-icon name="arrow"/>
                        </div>
                    </div>
                </div>
                <div class="pay-window">
                    <van-steps :active="order.status">
                        <van-step></van-step>
                        <van-step></van-step>
                        <van-step></van-step>
                        <van-step></van-step>
                        <van-step></van-step>
                    </van-steps>
                </div>
            </div>
        </div>
        <div>
            <van-panel :title="order.provider_detail.provider_name" :desc="order.provider_detail.company_phone">
                <van-card
                    v-for="(item,index) in order.order_product"
                    :key="index"
                    :price="item.discount_price"
                    :title="item.product_name"
                    :thumb="item.img_url"
                    :desc="' *'+item.num"
                >
                    <div slot="tags">
                        <div v-for="(sku,skuIndex) in item.sku_info" :key="skuIndex">
                            <span>{{sku.sku_key}}</span>
                            <van-tag type="danger">{{sku.sku_name}}</van-tag>
                        </div>
                    </div>
                </van-card>
            </van-panel>

        </div>
        <div>
            <van-cell-group>
                <van-cell center title="" :value="``+order.total_fee"/>
                <van-cell center title="" :value="order.shipping_fee==0?``:order.shipping_fee+''"/>
                <van-panel title="" :status="`${parseFloat(order.total_fee)+parseFloat(order.shipping_fee)}`">
                </van-panel>
            </van-cell-group>
        </div>
        <div class="default-window text-right">
            <van-button  @click="closePay" v-if="order.status==0"></van-button>
            <van-button type="danger" @click="confirmOrder" v-if="order.status==2"></van-button>
        </div>
        <div>
            <!--        -->
            <div style="width: 100%;height: 50px"></div>
            <van-submit-bar
                v-if="order.status==0"
                :price="(parseFloat(order.total_fee)+parseFloat(order.shipping_fee))*100"
                button-text=""
                text-align="left"
                button-type="danger"
                :loading="buyButtonStatus"
                @submit="payOrder"
            />
        </div>
    </div>
</template>

<script>


    export default {
        name: "OrderDetail",
        data() {
            return {
                buyButtonStatus: false,
                order: {
                    provider_detail: {
                        provider_name: '',
                        company_phone: '',
                    }
                }
            }
        },
        mounted() {
            this.loadOrderDetail();
        },
        methods: {
            //
            confirmOrder(){
                this.$dialog.confirm({
                    message: '',
                    confirmButtonColor: '#ee0a24',
                }).then(() => {
                    let params = {
                        order_no: this.order.order_no
                    };

                    this.$api('Order/confirm',params).then(data=>{
                        this.$toast.success('');
                        this.loadOrderDetail();
                    });
                }).catch(() => {
                });
            },
            //
            closePay(){
                this.$dialog.confirm({
                    message: '',
                    confirmButtonColor: '#ee0a24',
                }).then(() => {
                    let params = {
                        order_no: this.order.order_no
                    };
                    this.$api('Order/cancel',params).then(data=>{
                        this.$toast.success('');
                        this.loadOrderDetail();
                    });
                }).catch(() => {
                });
            },
            //
            loadOrderDetail() {
                let params = {
                    order_no: this.$route.params.loadId
                };
                this.$api('Order/detail', params).then(data => {
                    console.log(data);
                    this.order = data.order_detail;
                });
            },
            payOrder() {
                this.$dialog.confirm({
                    message: '',
                    confirmButtonColor: '#ee0a24',
                }).then(() => {
                    this.buyButtonStatus = true;
                    let params = {
                        no: this.order.order_no
                    };
                    this.$api('Pay/pay', params).then(res => {
                        this.postData(res.response);
                    });
                }).catch(() => {
                });
            },

            /**
             * @method:ajax
             */
            postData: function (params) {

                let vm = this;

                let data = JSON.parse(params);
                vm.weixinPay(data);
            },
            /**
             * @method :
             * @param data
             */
            weixinPay: function (data) {
                let vm = this;
                if (typeof WeixinJSBridge == "undefined") {
                    //
                    if (document.addEventListener) {
                        document.addEventListener(
                            "WeixinJSBridgeReady",
                            vm.onBridgeReady(data),
                            false
                        );
                    } else if (document.attachEvent) {
                        document.attachEvent(
                            "WeixinJSBridgeReady",
                            vm.onBridgeReady(data)
                        );
                        document.attachEvent(
                            "onWeixinJSBridgeReady",
                            vm.onBridgeReady(data)
                        );
                    }
                } else {
                    vm.onBridgeReady(data);
                }
            },
            /**
             * @method 
             * @param data:,(APIH5);
             */
            onBridgeReady: function (data) {
                let vm = this;
                WeixinJSBridge.invoke(
                    "getBrandWCPayRequest",
                    {
                        appId: data.appId, //
                        timeStamp: data.timeStamp, //1970
                        nonceStr: data.nonceStr, //
                        package: data.package,
                        signType: data.signType, //
                        paySign: data.paySign //
                    },
                    function (res) {
                        // ,res.err_msgok
                        if (res.err_msg == "get_brand_wcpay_requestok") {
                            this.$toast.success("");
                            this.buyButtonStatus = false;
                            this.loadOrderDetail();
                        } else {
                            this.$notify("");
                            this.buyButtonStatus = false;
                            this.loadOrderDetail();
                        }
                    }
                );
            },
        }
    }
</script>

<style scoped>
    .status-window {
        width: 100%;
        background-color: #ee0a24;
        color: #FFFFFF;
        padding: 10px;
        box-sizing: border-box;
    }

    .info-window {
        border-radius: 8px;
        box-shadow: 2px 2px 8px #a5a5a5;
    }

    .pay-window {
        padding: 10px;
        border-radius: 8px;
    }

    .address-window {
        padding: 10px;
        border-radius: 8px;
    }

    .address-left {
        width: 35px;
    }
</style>



