<template>
    <div>
        <div class="default-window text-right">

        </div>
        <div class="default-window text-center">
            <img :src="formData.headimgurl" class="head" alt="">
        </div>
        <div class="default-window text-center">
            <span style="font-size: 22px">{{formData.nickname}}</span>
        </div>
<!--        <div class="text-center font-gary" style="padding: 0 10px">
            <div><span>{{formData.level_text}} / {{formData.invest}}</span></div>
            <div><span>{{formData.sn}}</span></div>
        </div>-->
        <van-cell-group title="">
            <van-field label="" v-model="formData.ali_pay" placeholder=""/>
            <van-field label="" v-model="formData.real_name" placeholder=","/>
        </van-cell-group>
        <van-cell-group title="">
            <van-field label="" v-model="formData.bank_name" placeholder=":"/>
            <van-field label="" v-model="formData.bank_user" placeholder=""/>
            <van-field label="" v-model="formData.bank_account" placeholder=""/>
        </van-cell-group>

        <div class="default-window">
            <van-button @click="save()" type="danger" block round></van-button>
        </div>

    </div>
</template>

<script>
    export default {
        name: "UserData",
        data() {
            return {
                nickname: 'tobeyour1004',

                userDetail: {},
                formData: {}
            }
        },
        mounted() {

            this.loadUserCenter();
        },
        methods: {

            //
            loadUserCenter() {
                this.$api('UserData/index').then(data => {
                    console.log(data);
                    this.formData = data.user_data;
                });
            },
            // 
            save() {
                this.$api('UserData/save', this.formData).then(data => {
                    this.$toast.success('');
                });
            }
        }
    }
</script>

<style scoped>
    .head {
        width: 100px;
        height: 100px;
        border-radius: 100%;
    }
</style>



